import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { Button, Icon, Form, Select, Row, Col, Checkbox } from 'antd';
import VisaHeader from '@bit/jasonhendricksdev.visa.visa-header'
import VisaBanner from '@bit/jasonhendricksdev.visa.visa-banner'
import VisaSelect from 'components/VisaSelect'
import VisaInput from 'components/VisaInput'
import { DS160 } from '../../actions/types'
import resources, { translate } from 'utils/resources'
import * as constants from 'utils/constants'
import * as utils from 'utils'

const { Option, OptGroup } = Select

import './index.scss'

const MyForm = ({
  agency, history, resetState, initLang = 'en-US', form,
}) => {
  const { getFieldDecorator, getFieldValue, setFieldsValue } = form
  const [lang, setLanguage] = useState('en-US')
  const tr = (r) => translate(r, lang);

  // const [b_conditions, checkConditions] = useState(false)
  // const [b_processing, checkProcessing] = useState(false)
  // const [b_email_notification, checkEmailNotification] = useState(false)

  useEffect(() => {
    setLanguage(initLang)
  }, [initLang])

  function onStartApplication(e, form) {
    e.preventDefault();
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
          console.log(values)
          if(agency) {
            history.push({
              pathname: '/visa/application-form', 
              search: `?agency=${agency}`
            });  
          } else {
            history.push('/visa/application-form')
          }
          resetState(DS160.DS160_INIT_STATE, {
            ds160: {
              b_conditions: values.data.b_conditions,
              b_processing: values.data.b_processing,
              b_email_notification: values.data.b_email_notification,
              start: {
                language: lang,
                citizenCode: values.data.citizenCode,
                visitArea: values.data.visitArea
              },
              register: {
                email: values.data.email,
                emailConfirmation: values.data.emailConfirmation,
                password: values.data.password,
                confirmPassword: values.data.confirmPassword,
              },
            }
          })
      }
    });
  }

  function onChangeCitizenCode(e) {
    setFieldsValue({ "data.visitArea" : undefined })
  }

  // function onStartApplication() {
  //   if(agency) {
  //     history.push({
  //       pathname: '/visa/application-form', 
  //       search: `?agency=${agency}`
  //     });  
  //   } else {
  //     history.push('/visa/application-form')
  //   }
  //   resetState(DS160.DS160_INIT_STATE, { ds160: { start: {language: lang} } });
  // }

  function onChangeLang(lang) {
    setLanguage(lang)
  }

  function validatePwd(rule, value, callback) {
    if(!value) {
      callback(tr(resources.validations.required))
      return
    }
    if(value.length < 6) {
      callback(tr(resources.validations.validatePwd.short));
      return;
    }
    if(!/\d/.test(value) || !/[A-Za-z]/.test(value)) {
      callback(tr(resources.validations.validatePwd.complexity))
      return;
    }
    callback();
  }

  function validatePwdConfirm(rule, value, callback) {
    if(!value) {
      callback(tr(resources.validations.required))
      return
    }
    if(value != getFieldValue('data.password')) {
      callback(tr(resources.validations.validatePwdConfirm.notmatch));
      return;
    }
    callback();
  }
  
  function validateEmailConfirm(rule, value, callback) {
    if(!value) {
      callback(tr(resources.validations.required))
      return
    }
    if(value != getFieldValue('data.email')) {
      callback(tr(resources.validations.validateEmailConfirm.notmatch));
      return;
    }
    callback();
  }

  const formItemLayout = {
    layout: 'vertical',
    labelCol: {
      xs: { span: 24 },
    },
    wrapperCol: {
      sm: { span: 24 },
      md: { span: 12 }
    },
  };

  const citizenCode = getFieldValue('data.citizenCode')

  let visitAreaType = constants.new_kdmid_visitArea_list['default']
  if(constants.new_kdmid_visitArea_list[citizenCode])
    visitAreaType = constants.new_kdmid_visitArea_list[citizenCode]

  return (
    <div className="visa-ds160">
      <VisaHeader />
      <VisaBanner>
        Russian e-visa Online Application
      </VisaBanner>
      <div className="visa-ds160__content container">
        <Form {...formItemLayout}>
          <div className="visa-global-field visa-global-border-bottom">
            <h2 className="visa-global-section-title ant-form-item-required">
              {tr(resources.step_1.section_title)}
            </h2>
          </div>

          <Form.Item label={tr(resources.language.label)} extra={tr(resources.language.extra)}>
            <Select placeholder="Select an Option" value={lang} onChange={onChangeLang} optionFilterProp="children" showSearch style={{ width: '300px' }}>
              <Option value="en-US">ENGLISH</Option>
              <Option value="fr-FR">FRANÇAIS (FRENCH)</Option>
              <Option value="es-ES">ESPAÑOL (SPANISH)</Option>
              <Option value="de-DE">DEUTSCH (GERMAN)</Option>
              <Option value="it-IT">ITALIANO (ITALIAN)</Option>
            </Select>
          </Form.Item>

          <Row>
            <Col md={{ span: 12 }}>
              <Form.Item label={tr(resources.citizenCode.label)} extra={tr(resources.citizenCode.extra)} wrapperCol={{ md: 12 }}>
                {getFieldDecorator('data.citizenCode', {
                  // initialValue: utils.getInitialValue(data.citizenCode),
                  rules: [{ required: true, message: tr(resources.validations.required) }],
                })(
                  <VisaSelect combines={constants.export_list(constants.new_kdmid_citizenCode_list)} onChange={onChangeCitizenCode}/>
                )}
              </Form.Item>
            </Col>
            <Col md={{ span: 12 }}>
              <Form.Item label={tr(resources.visitArea.label)} extra={tr(resources.visitArea.extra)} wrapperCol={{ md: 12 }}>
                {getFieldDecorator('data.visitArea', {
                  // initialValue: utils.getInitialValue(data.visitArea),
                  rules: [{ required: true, message: tr(resources.validations.required) }],
                })(
                  <Select placeholder="Choose area..." showSearch optionFilterProp="children">
                    {citizenCode && constants.export_opt_list(constants.new_kdmid_visitArea_list_types[visitAreaType]).map((optgroup, index) => {
                      return <OptGroup label={optgroup.label} key={index}>
                        {
                          optgroup.options.length ? 
                            optgroup.options.map((option, option_index) => <Option value={option.props.value} key={option_index}>{option.props.children}</Option>) : 
                            <Option value={optgroup.options.props.value} key="0">{optgroup.options.props.children}</Option>
                        }
                      </OptGroup>
                    })}
                  </Select>
                )}
                
              </Form.Item>
            </Col>
          </Row>

          <div className="visa-global-field visa-global-border-bottom">
            <h2 className="visa-global-section-title">
              {tr(resources.register.section_title)}
            </h2>
          </div>

          <Row>
            <Col md={{ span: 12 }}>
              <VisaInput 
                label={tr(resources.email.label)}
                extra={tr(resources.email.extra)}
                field="data.email"
                // initialValue={data.email}
                getFieldDecorator={getFieldDecorator}
                type="email"
                customRule={[{ type: 'email', required: true, message: 'Please input valid email address' }]}
                wrapperCol={{ md: 24 }}
              />
            </Col>
            <Col md={{ span: 12 }}>
              <VisaInput 
                label={tr(resources.email_confirm.label)}
                field="data.emailConfirmation"
                // initialValue={data.emailConfirmation}
                getFieldDecorator={getFieldDecorator}
                type="email"
                customRule={[{ validator: validateEmailConfirm }]}
                wrapperCol={{ md: 24 }}
              />
            </Col>
          </Row>
          
          <Row>
            <Col md={{ span: 12 }}>
              <VisaInput 
                label={tr(resources.pwd.label)}
                extra={tr(resources.pwd.extra)}
                field="data.password"
                // initialValue={data.password}
                getFieldDecorator={getFieldDecorator}
                type="password"
                customRule={[{ validator: validatePwd }]}
              />
            </Col>
            <Col md={{ span: 12 }}>
              <VisaInput 
                label={tr(resources.pwd_confirm.label)}
                extra={tr(resources.pwd_confirm.extra)}
                field="data.confirmPassword"
                // initialValue={data.confirmPassword}
                getFieldDecorator={getFieldDecorator}
                type="password"
                customRule={[{ validator: validatePwdConfirm }]}
              />
            </Col>
          </Row>

          <div className="visa-global-field visa-global-border-bottom">
            <h2 className="visa-global-section-title">
              {tr(resources.before.confirm.label)}
            </h2>
          </div>

          

          <Form.Item wrapperCol={{ md: 24 }}>
            {getFieldDecorator('data.b_processing', {
              // initialValue: utils.getInitialValue(data.b_processing),
              valuePropName: "checked",
              rules: [{
                required: true, 
                message: tr(resources.validations.required), 
                transform: value => (value || undefined),  // Those two lines
                type: 'boolean'
              }],
            })(
              <Checkbox>
                {tr(resources.before.confirm.b_processing)}
              </Checkbox>
            )}
          </Form.Item>

          <Form.Item wrapperCol={{ md: 24 }}>
            {getFieldDecorator('data.b_email_notification', {
              // initialValue: utils.getInitialValue(data.b_email_notification),
              valuePropName: "checked",
              rules: [{
                required: true, 
                message: tr(resources.validations.required), 
                transform: value => (value || undefined),  // Those two lines
                type: 'boolean'
              }],
            })(
              <Checkbox>
                {tr(resources.before.confirm.b_email_notification)}
              </Checkbox>
            )}
          </Form.Item>

          <Form.Item wrapperCol={{ md: 24 }}>
            {getFieldDecorator('data.b_conditions', {
              // initialValue: utils.getInitialValue(data.b_conditions),
              valuePropName: "checked",
              rules: [{
                required: true, 
                message: tr(resources.validations.required), 
                transform: value => (value || undefined),  // Those two lines
                type: 'boolean'
              }],
            })(
              <Checkbox>
                {tr(resources.before.confirm.b_conditions)}
              </Checkbox>
            )}
          </Form.Item>

          <div className="visa-global-btn-group" style={{textAlign: 'center', margin: '40px 0px'}}>
            <Button type="primary" onClick={(e) => onStartApplication(e, form)}>
              {tr(resources.before.start_button)}
              <Icon type="right" />
            </Button>
          </div>

          
        </Form>

        {/* <Form.Item label={tr(resources.language.label)} extra={tr(resources.language.extra)}>
          <Select placeholder="Select an Option" value={lang} onChange={onChangeLang} optionFilterProp="children" showSearch style={{ width: '300px' }}>
            <Option value="en-US">ENGLISH</Option>
            <Option value="fr-FR">FRANÇAIS (FRENCH)</Option>
            <Option value="es-ES">ESPAÑOL (SPANISH)</Option>
            <Option value="de-DE">DEUTSCH (GERMAN)</Option>
            <Option value="it-IT">ITALIANO (ITALIAN)</Option>
          </Select>
        </Form.Item>

        <Welcome lang={lang} />
        
        <Form.Item label={tr(resources.before.confirm.label)} required>
          <Checkbox onChange={(e)=>checkConditions(e.target.checked)} checked={b_conditions} style={{ marginLeft: '8px' }}>
            {tr(resources.before.confirm.b_conditions)}
          </Checkbox>
          <Checkbox onChange={(e)=>checkProcessing(e.target.checked)} checked={b_processing}>
            {tr(resources.before.confirm.b_processing)}
          </Checkbox>
          <Checkbox onChange={(e)=>checkEmailNotification(e.target.checked)} checked={b_email_notification}>
            {tr(resources.before.confirm.b_email_notification)}
          </Checkbox>
        </Form.Item> */}
        
        {/* <div className="visa-global-btn-group" style={{textAlign: 'center', margin: '40px 0px'}}>
          <Button type="primary" onClick={onStartApplication} disabled={!b_conditions || !b_processing || !b_email_notification}>
            {tr(resources.before.start_button)}
            <Icon type="right" />
          </Button>
        </div> */}
      </div>
      
    </div>
  )
}

const mapDispatchToProps = dispatch => {
  return {
    resetState: (type, initValue) => {
      dispatch({ type, initValue })
    },
  }
}
const DS160_HOME = Form.create()(MyForm)
export default withRouter(
  connect(
    null,
    mapDispatchToProps,
  )(DS160_HOME),
)
