import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import VisaBanner from '@bit/jasonhendricksdev.visa.visa-banner'
import VisaHeader from '@bit/jasonhendricksdev.visa.visa-header'
import { DS160 } from '../../../actions/types'
import Form_DS160_2 from './Step2';
import Form_Personal from './Step4_Personal';
import Form_Passport from './Step6_Passport';
import Form_Visit from './Step5_Visit';
import Form_Contacts_Occupations from './Step7_Contacts_Occupations';
import Form_Register from './Step3_Register';
import Form_Relatives from './Step8_Relatives';
import Form_Photo from './Photo';
import Form_Payment from './Payment';
import Form_Final from './Final';
import { Spin, notification, Progress } from 'antd';
import objectAssignDeep from 'object-assign-deep';
import ds160_validators from '../Validators';
import { withCookies } from 'react-cookie';

import moment from 'moment'
import './index.scss'

const openNotificationWithIcon = type => {
  notification[type]({
    message: 'Submit without payment',
    description:
      type == 'success' ? 'Successfully submitted. It may take few minutes to process' : 'Failed to submit',
  });
};

class DS160_Wizard extends Component {
  static defaultProps = {
    token: null
  }
  constructor(props) {
    super(props)
    console.log('constructor')
  }

  componentDidMount() {
    const { token } = this.props
    if( token )
      this.props.loadApplicationFromDB( DS160.DS160_GET_REQUEST, token )
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if( this.props.token != nextProps.token ) {
      if( nextProps.token )
        this.props.loadApplicationFromDB( DS160.DS160_GET_REQUEST, token )
    }
  }

  onPrev = (data, field) => {
    if(field != '')
      this.props.updateValues( DS160.DS160_UPDATE_VALUES, objectAssignDeep(this.props.ds160, {[field]: data}));
    else
      this.props.updateValues( DS160.DS160_UPDATE_VALUES, data );
    this.props.onPrevStep(DS160.DS160_PREV_STEP);

    const { agency } = this.props
    if(agency) {
      this.props.history.push({
        pathname: '/visa/application-form', 
        search: `?agency=${agency}`
      });  
    } else {
      this.props.history.push('/visa/application-form')
    }

    window.scrollTo(0, 0); 
  }

  onNext = (data, field) => {
    if(field != '')
      this.props.updateValues( DS160.DS160_UPDATE_VALUES, objectAssignDeep(this.props.ds160, {[field]: data}) );
    else
      this.props.updateValues( DS160.DS160_UPDATE_VALUES, data );
    this.props.onNextStep(DS160.DS160_NEXT_STEP);

    const { agency } = this.props
    if(agency) {
      this.props.history.push({
        pathname: '/visa/application-form', 
        search: `?agency=${agency}`
      });  
    } else {
      this.props.history.push('/visa/application-form')
    }

    window.scrollTo(0, 0); 
  }

  onSaveAndContinue = (data, field) => {
    const { agency } = this.props
    const payload = {
      email: '',
      completed: false,
      step_index: this.props.step_index,
      data: field != '' ? objectAssignDeep(this.props.ds160, {[field]: data }) : objectAssignDeep(this.props.ds160, data),
      agency: agency
    }
    this.props.onSaveAndContinueLater(DS160.DS160_SAVE_REQUEST, payload, this.props.applicationId, (result) => {
      if(agency) {
        this.props.history.push({
          pathname: '/visa/application-form-later', 
          search: `?agency=${agency}`
        });  
      } else {
        this.props.history.push('/visa/application-form-later')
      }
    })
  }

  onSubmit = (data, field) => {
    const { agency } = this.props
    const payload = {
      email: '',
      completed: true,
      step_index: this.props.step_index,
      data: field != '' ? objectAssignDeep(this.props.ds160, {[field]: data }) : objectAssignDeep(this.props.ds160, data),
      agency: agency
    }
    this.props.onSaveAndContinueLater(DS160.DS160_SAVE_REQUEST, payload, this.props.applicationId, (result) => {
      if(agency) {
        // window.location.href = `https://apply.usvisaappointments.com/us-visa-interview/`
      } else {
        window.location.href = `https://russia-evisa-application.com/checkout/?add-to-cart=16&application_number=${result.app_id}&token=${result._id}`
        // needs to implement correct order id
      }
    })
    
  }

  onSubmitWithoutPayment = (data, field) => {
    const { agency } = this.props
    const payload = {
      email: '',
      completed: true,
      step_index: this.props.step_index,
      withoutPayment: true,
      data: field != '' ? objectAssignDeep(this.props.ds160, {[field]: data }) : objectAssignDeep(this.props.ds160, data),
      agency: agency
    }
    this.props.onSaveAndContinueLater(DS160.DS160_SAVE_REQUEST, payload, this.props.applicationId, (result) => {
      openNotificationWithIcon('success')
    })
  }

  handleSubmitWithoutPayment = (e, form, handleDates, field) => {
    e.preventDefault();
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        if( handleDates )
          this.onSubmitWithoutPayment(handleDates(values.data), field)
        else 
          this.onSubmitWithoutPayment(values.data, field)
      }
    });
  }

  handleSubmit = (e, form, handleDates, field) => {
    e.preventDefault();
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        if( handleDates )
          this.onSubmit(handleDates(values.data), field)
        else 
          this.onSubmit(values.data, field)
      }
    });
  }

  handlePrev = (e, form, handleDates, field) => {
    e.preventDefault();
    const values = form.getFieldsValue();
    if(handleDates)
      this.onPrev(handleDates(values.data), field)
    else
      this.onPrev(values.data, field)
  }

  handleSave = (e, form, handleDates, field) => {
    e.preventDefault();
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        if( handleDates )
          this.onSaveAndContinue(handleDates(values.data), field)
        else 
          this.onSaveAndContinue(values.data, field)
      }
    });
  }

  handleNext = (e, form, handleDates, field) => {
    e.preventDefault();
    console.log(field, handleDates, form)
    form.validateFieldsAndScroll((err, values) => {
      console.log(err)
      if (!err) {
        console.log(values)
        if(handleDates)
          this.onNext(handleDates(values.data), field);
        else
          this.onNext(values.data, field);
      }
    });
  };

  onChangeCitizenCode = () => { 
    this.props.initVisitArea(DS160.DS160_INIT_VISIT_AREA)
  }

  render() {
    const { step_index, ds160, loading, token, agency } = this.props

    if(loading) {
      return <Spin tip="Please wait..." id="visa-ds160-save-and-continue-spin">
      </Spin>
    }

    let form_render = ''

    let fields_list = [
      null, 
      "personal", 
      "visit", 
      "passport", 
      "contacts_occupations",
      "relatives",
      "photo",
      "payment",
      "final"
    ]

    let field = fields_list[step_index]

    let shared_params = {
      handlePrev: (e, form, handleDates) => this.handlePrev(e, form, handleDates, field),
      handleNext: (e, form, handleDates) => this.handleNext(e, form, handleDates, field),
      handleSave: (e, form, handleDates) => this.handleSave(e, form, handleDates, field),
      validators: ds160_validators,
      agency: agency,
      date_birth: ds160.personal.birth_date,
      lang: ds160.start.language
    }

    switch(field) {
      // case 'register':
      //   form_render = <Form_Register {...shared_params} data={ds160.register} />
      //   break;
      case 'personal':
        form_render = <Form_Personal {...shared_params} showPrev={false} data={ds160.personal} citizenCode={ds160.start.citizenCode} onChangeCitizenCode={this.onChangeCitizenCode}/>
        break;
      case 'visit':
        form_render = <Form_Visit {...shared_params} data={ds160.visit} citizenCode={ds160.personal.citizenCode}/>
        break;
      case 'passport':
        form_render = <Form_Passport {...shared_params} data={ds160.passport}/>
        break;
      case 'contacts_occupations':
        form_render = <Form_Contacts_Occupations {...shared_params} data={ds160.contacts_occupations}/>
        break;
      case 'relatives':
        form_render = <Form_Relatives {...shared_params} data={ds160.relatives}/>
        break;
      case 'photo':
        form_render = <Form_Photo {...shared_params} data={ds160.photo}/>
        break;
      case 'payment':
        form_render = <Form_Payment {...shared_params} data={ds160.payment}/>
        break;
      case 'final':
        form_render = <Form_Final {...shared_params} 
            handleSubmit={(e, form, handleDates) => this.handleSubmit(e, form, handleDates, field)} 
            handleSubmitWithoutPayment={(e, form, handleDates) => this.handleSubmitWithoutPayment(e, form, handleDates, field)}/>
        break;
    }  

    return (
      <div className="visa-ds160">
        <VisaHeader className={ step_index == 1 ? "visa-com-header-first" : "visa-com-header-not-first" }/>
        <VisaBanner backgroundColor="#428bca" className={ step_index == 1 ? "visa-com-banner-first" : "visa-com-banner-not-first" }>
          { step_index == 1 ? 'Important: Before You Start' : 'Russian e-visa Online Application' }
        </VisaBanner>
        <Progress strokeColor={{ '0%': '#108ee9', '100%': '#87d068' }} percent={parseInt(step_index * 100.0 / (fields_list.length - 1))} status="active" style={{ width: '80%', left: '10%'}}/>
        <div className="visa-ds160__content container">
          {form_render}
        </div>
        
      </div>
    )
  }
}


const mapDispatchToProps = dispatch => {
  return {
    onNextStep: (type) => {
      dispatch({ type })
    },
    onPrevStep: (type) => {
      dispatch({ type })
    },
    updateValues: (type, values) => {
      dispatch({ type, values })
    },
    onSaveAndContinueLater: (type, payload, applicationId, cb) => {
      dispatch({ type, payload, applicationId, cb })
    },
    loadApplicationFromDB: (type, applicationId) => {
      dispatch({ type, applicationId })
    },
    initVisitArea: (type) => {
      dispatch({ type })
    }
  }
}

const mapStateToProps = state => ({
  step_index: state.newkdmid.step_index,
  ds160: state.newkdmid.ds160,
  loading: state.newkdmid.loading,
  applicationId: state.newkdmid.applicationId,  
})


export default withCookies(withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(DS160_Wizard),
))
