import React, { Component } from "react";
import { Form, Button, Select, Checkbox, Input, Radio, DatePicker, Row, Col } from 'antd';
import * as constants from '../../../../utils/constants'
import VisaRadio from "../../../../components/VisaRadio";
import VisaInput from "../../../../components/VisaInput";
import VisaSelectItem from "../../../../components/VisaSelectItem";
import VisaDatePicker from "../../../../components/VisaDatePicker";
import * as utils from '../../../../utils'
import VisaInputArray from "../../../../components/VisaInputArray";
import resources from '../../../../utils/resources'
import { translate } from '../../../../utils/resources'
import VisaSelect from "../../../../components/VisaSelect";

const { Option } = Select;
const { TextArea } = Input;

class MyForm extends Component {
  static defaultProps = {
    showPrev: true,
    showNext: true,
  }
  
  handleDates = (data) => {
    if(data.birth_date)
      data.birth_date = data.birth_date.format('DD.MM.YYYY')
    return data
  }

  render() {
    const { getFieldDecorator, isFieldTouched, setFieldsValue, getFieldValue } = this.props.form;
    const formItemLayout = {
      layout: 'vertical',
      labelCol: {
        xs: { span: 24 },
      },
      wrapperCol: {
        sm: { span: 24 },
      },
    };

    const { showPrev, showNext, onPrev, onNext, data, lang, citizenCode } = this.props

    getFieldDecorator('data.b_other_names', { initialValue: utils.getInitialValue(data.b_other_names) })
    
    const tr = (r) => translate(r, lang);

    return (
      <Form {...formItemLayout}>
        <div className="visa-global-field visa-global-border-bottom">
          <h2 className="visa-global-section-title">{tr(resources.personal.section_title)}</h2>
        </div>
        <Form.Item label={tr(resources.personal.citizenCode.label)} extra={tr(resources.personal.citizenCode.extra)}>
          {getFieldDecorator('data.citizenCode', {
            initialValue: utils.getInitialValue(data.citizenCode ? data.citizenCode : citizenCode),
            rules: [{ required: true, message: tr(resources.validations.required) }],
          })(
            <VisaSelect combines={constants.export_list(constants.new_kdmid_citizenCode_list)} onChange={this.props.onChangeCitizenCode}/>
          )}
        </Form.Item>
        <Row gutter={16}>
          <Col xs={{ span: 24 }} md={{ span: 12 }}>
            <VisaInput 
              label={tr(resources.personal.surname.label)}
              extra={tr(resources.personal.surname.extra)}
              field="data.surname"
              initialValue={data.surname}
              getFieldDecorator={getFieldDecorator}
              customRule={[{ validator: (rule, value, callback) => this.props.validators.validateName(rule, value, callback, "Surname", true) }]}
              
            />
          </Col>
          <Col xs={{ span: 24 }} md={{ span: 12 }}>
            <VisaInput 
              label={tr(resources.personal.firstnames.label)}
              extra={tr(resources.personal.firstnames.extra)}
              field="data.firstnames"
              initialValue={data.firstnames}
              getFieldDecorator={getFieldDecorator}
              customRule={[{ validator: (rule, value, callback) => this.props.validators.validateName(rule, value, callback, "First name, middle names, patronymic names", true) }]}
            />
          </Col>
        </Row>

        <VisaRadio
          label={tr(resources.personal.b_other_names.label)}
          extra={tr(resources.personal.b_other_names.extra)}
          field="data.b_other_names"
          initialValue={data.b_other_names}
          getFieldDecorator={getFieldDecorator}
        />

        {
          getFieldValue('data.b_other_names') == 'yes' && 
          <VisaInputArray 
            label="Provide a list of Last/first/middle names"
            getFieldDecorator={getFieldDecorator}
            getFieldValue={getFieldValue}
            setFieldsValue={setFieldsValue}
            initialValue={data.other_names}
            arrayField="data.other_names"
            keysField="copy.other_names"
            validators={this.props.validators}
            maxLength={60}
            // customRule={[{ validator: (rule, value, callback) => this.props.validators.validateNumber(rule, value, callback, "Phone number", true) }]}
          />
        }
        <Row gutter={16}>
          <Col xs={{ span: 24 }} md={{ span: 6 }}>
            <Form.Item label="Sex" required>
              {getFieldDecorator('data.sex', {
                initialValue: utils.getInitialValue(data.sex),
                rules: [{ required: true, message: tr(resources.validations.required) }],
              })(
                <Radio.Group>
                  <Radio value='M'>Male</Radio>
                  <Radio value='F'>Female</Radio>
                </Radio.Group>
              )}
            </Form.Item>
          </Col>
          <Col xs={{ span: 24 }} md={{ span: 12 }}>
            <VisaDatePicker 
              label={tr(resources.personal.birth_date.label)}
              field="data.birth_date"
              initialValue={data.birth_date}
              getFieldDecorator={getFieldDecorator}
              customRule={[{ validator: (rule, value, callback) => this.props.validators.validateEarlierDate(rule, value, callback, tr(resources.personal.birth_date.label), true) }]} 

              setFieldsValue={setFieldsValue}
              getFieldValue={getFieldValue}
            />
          </Col>
        </Row>
        <VisaInput 
          label={tr(resources.personal.birth_place.label)}
          extra={tr(resources.personal.birth_place.extra)}
          field="data.birth_place"
          initialValue={data.birth_place}
          getFieldDecorator={getFieldDecorator}
          customRule={[{ validator: (rule, value, callback) => this.props.validators.validateCyrillic(rule, value, callback, tr(resources.personal.birth_place.label), true) }]} 
          
        />

        <div className="visa-form-bottom-btn-group">
          {showPrev && <Button style={{ marginRight: 8 }} onClick={(e) => this.props.handlePrev(e, this.props.form, this.handleDates)}>{tr(resources.prev)}</Button>}
          {showNext && <Button type="primary" onClick={(e) => this.props.handleNext(e, this.props.form, this.handleDates)}>{tr(resources.next)}</Button>}
          <Button type="link" onClick={(e) => this.props.handleSave(e, this.props.form, this.handleDates)}>{tr(resources.save_and_continue_later)}</Button>
        </div>
      </Form>

    );
  }
}
const Form_Personal = Form.create()(MyForm)
export default Form_Personal;
