export const export_list = (array) => {
    return array.map(option => {
        return {
            value: option.props.value,
            label: option.props.children
        }
    })
}

export const export_opt_list = (array) => {
    return array.map(optgroup => {
        return {
            label: optgroup.props.label,
            options: optgroup.props.children
        }
    })
}

